.App-header {
  background-color: #161616;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.File-upload-view {
  width: 230px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

input[type="file"], .File-link {
  display: none;
}

.File-upload-actions {
  display: flex;
  flex-direction: column;
  width: 100%;
  justify-content: center;
}